import React, { Component } from 'react';
import '../style.css';
import './room.css';
import Nav from '../comment/nav/nav';
import RoomMess from '../comment/roomMess/roomMess';
import OpenRedBag from '../comment/openredbag/openredbag';
import OpenBoaBag from '../comment/openredbag/openboabag';
import Alerts from '../comment/alerts/alerts';
class App extends Component {
	constructor(props) {
    super(props);
    this.state = {
    	openRedBag:[],
    	openBoaBag:[],
    	openAlerts:[]
			
    };
  }
	redBagClick(type){
		if('openRedBag'==type){
			this.setState({openRedBag:[<OpenRedBag callBack={this.cancelOpenRedBag.bind(this)}></OpenRedBag>]});
		}else if('cancelRedBag'==type){
			this.setState({openRedBag:[]});
		}else if('cancelBoaBag'==type){
			this.setState({openBoaBag:[]});
		}else if('openBoaBag'==type){
			this.setState({openBoaBag:[<OpenBoaBag callBack={this.cancelBoaBag.bind(this)}></OpenBoaBag>]});
		}else if('openAlerts'==type){
			this.setState({openAlerts:[<Alerts callBack={this.cancelRule.bind(this)}></Alerts>]})
		}else if('cancel'==type){
			this.setState({openAlerts:[]});
		}
	}
	cancelOpenRedBag(){
		this.redBagClick('cancelRedBag');
	}
	cancelBoaBag(){
		this.redBagClick('cancelBoaBag');
	}
	cancelRule(){
		console.log('-=-=-=')
		this.redBagClick('cancel');
	}
  render() {
    return (
      <div className="app">
        <Nav></Nav>
        
        <RoomMess callBack={this.redBagClick.bind(this)}></RoomMess>
      	{this.state.openRedBag}
      	{this.state.openBoaBag}
      	{this.state.openAlerts}
      </div>
    );
  }
}
export default App;
