import React, { Component } from 'react';
import './openredbag.css';
import '../../style.css';
import {boaOpen,boaUserPic,boaBg} from '../../public';
class OpenBoaBag extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	
    };
    
  }
	render(){
		return(
			<div className="open-wrap">
				<div className="open-box">
					<div className="img-box">
						<img src={boaBg}/>
					</div>
					<div className="open-intro">
						<div className="cancel-box">
							<span onClick={this.props.callBack.bind(this)} className="boa-cancel glyphicon glyphicon-remove"></span>

						</div>
						<img className="bag-icon boa-icon" src={boaUserPic}/>
						<p className="boa-p1 p1">超级红包</p>
						<p className="all-eos">随机获取幸运大奖</p>
						<img className="bag-icon boa-icon boa-pen-icon" src={boaOpen}/>
					</div>
				</div>
			</div>
		)
	}
}

export default OpenBoaBag;