import React, { Component } from 'react';
import '../../style.css';
import './alerts.css';
class Alerts extends Component{
	constructor(props) {
    super(props);
    this.state = {

    };
    
  }

	render(){
		
		return (
			<div className="alert-wrap">
				<div className="alert-box">
					<div className="alert-cancel">
					<span onClick={this.props.callBack.bind(this)} className="glyphicon glyphicon-remove"></span>

					</div>
					<div className="alert-title">幸运池规则</div>
					<div className="alert-content">
						用户每参与3局抢红包游戏,将会获得一次打开幸运红包机会.每个红包含有(0.05-0.08)*房间等级的eos	
					</div>
				</div>
			</div>
		)
	}
}
export default Alerts;